  printPDF() {

    let { data, header } = this.props
    let { costcenter, remake, left_licence, right_licence, value } = this.state

    // console.log(header)

    let docDefinition = {

      content: [
        this.renderFrom(data, header, costcenter, remake, left_licence, right_licence, value)
      ],



      styles: {
        tableHeader: {
          bold: true,
          fontSize: 11,
          color: 'black'
        }
      },
      info: {
        title: 'awesome Document',
      },
      pageOrientation: 'landscape',
      pageMargins: [30, 10, 30, 10],
      defaultStyle: {
        font: 'THSarabunNew',
        fontSize: 11
      },

    };



    pdfMake.createPdf(docDefinition).open()
  }
